export default function translateMatchStatus(status: string): string {
  switch (status) {
    case 'SCHEDULED':
      return '';
      break;
    case 'LIVE':
      return '  ';
      break;
    case 'IN_PLAY':
      return ' ';
      break;
    case 'PAUSED':
      return '';
      break;
    case 'FINISHED':
      return '';
      break;
    case 'POSTPONED':
      return '';
      break;
    case 'SUSPENDED':
      return '';
      break;
    case 'CANCELED':
      return '';
      break;
    default:
      return '';
      break;
  }
}
